function reviewsSlider() {
    var swiper = new Swiper('.reviews .swiper-container', {
        slidesPerView: 2,
        spaceBetween: 0,
        navigation: {
            nextEl: '.reviews .swiper-button-next',
            prevEl: '.reviews .swiper-button-prev',
        },
        pagination: {
            el: '.reviews .swiper-pagination',
            type: 'custom',
            renderCustom: function (swiper, current, total) {
                return returnNum(current) + ' / ' +  returnNum(total);
            }
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            576: {
                slidesPerView: 2,
            },
        }
    })
}

function privilegeSlider() {
    var swiper = new Swiper('.privilege-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        navigation: {
            nextEl: '.privilege-slider .swiper-button-next',
            prevEl: '.privilege-slider .swiper-button-prev',
        },
        pagination: {
            el: '.privilege-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        on: {
            init: function () {
                setPaginationNums(this)
            },
            slideChange: function () {
                setPaginationNums(this)
            },
        },
    })


}

reviewsSlider()
privilegeSlider()

function returnNum(num) {
    if(+num < 10){
        return `0${num}`
    }

    return num
}

function setPaginationNums(swiper) {
    let current = returnNum(swiper.activeIndex+1)
    let all = returnNum($(".privilege-slider .swiper-slide").length)
    $(".privilege-slider__current").html(current)
    $(".privilege-slider__total").html(all)
}
